export default {
  props: {
    value: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      default: 'Search'
    }
  },
  data: function data() {
    return {
      search: this.value
    };
  },
  watch: {
    search: function search(val) {
      // console.log(val)
      this.$emit('search', val);
    }
  }
};